import React from "react";
import classNames from "classnames";
import { GRID_SIZE, PANEL_TYPE, TYPE } from "../constants";
import { useStore } from "../store/context";

const DEFAULTS = {
	ITEM_COLSPAN: 1,
	MAX_SIZE: 12,
	PARENT_ITEM_SPAN: 6,
};

const getColSpan = (col, parentItemSpan) => {
	const widgetAttrs = col.widgetAttrs ? JSON.parse(col.widgetAttrs) : {};
	if (widgetAttrs.colSpan) {
		return Number(widgetAttrs.colSpan);
	} else {
		return Number(parentItemSpan) || col.colSpan || DEFAULTS.PARENT_ITEM_SPAN;
	}
};

// get Column cell CSS according to colSpan and Container size
export const getColumnCss = (columns, colSpan) => {
	const [n, span] = [Number(columns), Number(colSpan)];
	if (n > GRID_SIZE.max) return "";
	return `col-span col-${
		span > n ? GRID_SIZE.max : Math.floor((GRID_SIZE.max * span) / n)
	}`;
};

const getFullGrid = (
	items,
	size = DEFAULTS.MAX_SIZE,
	isStackLayout,
	parentItemSpan,
	isSpacer,
	draggedWidgetAttrsColSpan
) => {
	const grid = [];
	const isStack = size === 1 || isStackLayout;
	const gridSize = isStack ? DEFAULTS.MAX_SIZE : size;
	const _parentItemSpan = parentItemSpan || DEFAULTS.PARENT_ITEM_SPAN;
	let row = [];
	let span = 0;

	const getItemColspan = (colSpan) => {
		// if(Number(parentItemSpan)) {
		// 	return Number(parentItemSpan)
		// }
		return Number(colSpan);
	};

	if (!items.length) {
		let remainingSpan = gridSize;
		const row = [];
		const $cell = { id: 0, separator: true };

		while (remainingSpan > 0) {
			row.push($cell);
			const colSpan =
				isStack || isSpacer
					? gridSize
					: remainingSpan < _parentItemSpan
					? remainingSpan
					: _parentItemSpan;
			row.push({
				id: 0,
				dummy: true,
				dummyPanel: true,
				colSpan: Number(colSpan),
			});
			remainingSpan -= colSpan;
			if (remainingSpan <= 0) {
				row.push($cell);
			}
		}

		return [row];
	}

	const addRow = ({ item, overflow = false } = {}) => {
		if (overflow) {
			let fillColspan = gridSize - (span - getItemColspan(item.colSpan));
			if (isSpacer) {
				row.push({
					id: 0,
					dummy: true,
					colSpan: fillColspan,
				});
			} else {
				while (fillColspan > 0) {
					if (fillColspan > (draggedWidgetAttrsColSpan || _parentItemSpan)) {
						row.push({
							id: 0,
							dummy: true,
							colSpan: draggedWidgetAttrsColSpan || _parentItemSpan,
						});
					} else {
						row.push({
							id: 0,
							dummy: true,
							colSpan: fillColspan,
						});
					}
					fillColspan -= draggedWidgetAttrsColSpan || _parentItemSpan;
				}
			}
		}
		grid.push(row);
		row = [];
		span = 0;
	};

	const addItem = (item) => row.push(item);

	for (let i = 0; i < items.length; i++) {
		const item = Object.assign(
			items[i],
			isStack
				? { colSpan: DEFAULTS.MAX_SIZE }
				: { colSpan: getItemColspan(getColSpan(items[i], parentItemSpan)) }
		);
		const { colSpan: itemSpan = DEFAULTS.ITEM_COLSPAN } = item;
		const isLastItem = i === items.length - 1;
		span += getItemColspan(itemSpan);
		if (span > gridSize) {
			addRow({ item, overflow: true });
			span = itemSpan;
			addItem(item);
		} else if (span === gridSize) {
			row.push(item);
			addRow({ item });
		} else {
			row.push(item);
		}

		if (isLastItem && row.length) {
			const occupiedSpan = row.reduce(
				(total, { colSpan = DEFAULTS.ITEM_COLSPAN }) => total + colSpan,
				0
			);
			if (isSpacer) {
				const remainSpan = gridSize - occupiedSpan;
				if (remainSpan > 0) {
					row.push({
						id: 0,
						dummy: true,
						colSpan: remainSpan,
					});
				}
			} else {
				let remainSpan = gridSize - occupiedSpan;
				while (remainSpan > 0) {
					if (remainSpan <= (draggedWidgetAttrsColSpan || _parentItemSpan)) {
						row.push({
							id: 0,
							dummy: true,
							colSpan: remainSpan,
						});
					} else {
						row.push({
							id: 0,
							dummy: true,
							colSpan: Number(draggedWidgetAttrsColSpan || _parentItemSpan),
						});
					}
					remainSpan -= draggedWidgetAttrsColSpan || _parentItemSpan;
				}
			}
			grid.push(row);
		}
	}

	const fullGrid = [];
	for (let i = 0; i < grid.length; i++) {
		const row = grid[i];
		const draggable = ![TYPE.menubar, TYPE.toolbar].includes(row[0]?.type);
		const upRow = [];
		const midRow = [];
		const addCell = ($row, options = { separator: true }) =>
			$row.push({
				id: 0,
				colSpan: 1,
				...options,
			});
		addCell(upRow);

		draggable &&
			addCell(midRow, {
				relatedColSpan: getColSpan(row[0], parentItemSpan),
				separator: true,
			});

		for (let j = 0; j < row.length; j++) {
			const col = row[j];
			const colSpan = getColSpan(col, parentItemSpan);
			addCell(midRow, col);

			draggable &&
				addCell(midRow, { relatedColSpan: colSpan, separator: true });
		}
		if (row.length) {
			if (isSpacer) {
				addCell(upRow, { dummy: true, colSpan: gridSize });
				addCell(upRow, {
					relatedColSpan: gridSize,
					separator: true,
				});
			} else {
				let totalSpan = gridSize;
				while (totalSpan > 0) {
					addCell(upRow, {
						dummy: true,
						colSpan: Number(
							totalSpan <= (draggedWidgetAttrsColSpan || _parentItemSpan)
								? totalSpan
								: draggedWidgetAttrsColSpan || _parentItemSpan
						),
					});
					addCell(upRow, {
						relatedColSpan: Number(_parentItemSpan),
						separator: true,
					});
					totalSpan -= draggedWidgetAttrsColSpan || _parentItemSpan;
				}
			}
		}

		draggable ? fullGrid.push(upRow) : fullGrid.push([]);
		fullGrid.push(midRow);

		if (i === grid.length - 1) {
			fullGrid.push(upRow);
		}
	}
	return fullGrid;
};

// Grid Container embeds grid cell
export const GridContainer = ({ className, attrs, children, ...rest }) => (
	<div
		className={classNames(
			className,
			attrs.cols ? `grid-span grid-${attrs.cols}` : ""
		)}
		{...rest}
	>
		{children}
	</div>
);

export function useGridWidget() {
	const [WidgetComponent, setWidgetComponent] = React.useState(() => null);

	React.useEffect(() => {
		import("./Widget").then((module) =>
			setWidgetComponent(() => module.default)
		);
	}, []);

	return WidgetComponent;
}

/**
 * Grid Component handle layout of Container Components like panels
 */
function Grid(props) {
	const WidgetComponent = useGridWidget();
	const {
		state: { widgets: _widgets, customFieldWidgets, dragWidgetProps },
	} = useStore();
	const isSpacer = dragWidgetProps?.attrs?.serverType === "spacer";
	const draggedWidgetAttrsColSpan =
		typeof dragWidgetProps?.id === "symbol" &&
		["one-to-many", "many-to-many"].includes(dragWidgetProps?.attrs?.serverType)
			? undefined //Tempory fix for AOP issue
			: JSON.parse(dragWidgetProps?.attrs?.widgetAttrs || "{}").colSpan;
	const { className, items, attrs = {}, panelId, design } = props;
	let widgets = props._type === "customField" ? customFieldWidgets : _widgets;
	const widgetAttrs = JSON.parse(attrs.widgetAttrs || "{}");
	const gridSize = Number(attrs.cols || 1);
	const columns = gridSize * 2 + 1;
	const isStackLayout = attrs.layout === PANEL_TYPE.stack;
	const spacers = columns === 1 ? 0 : columns / 2 + 1;
	const spacerWidth = design ? 1 : 0; //%
	const cols = (columns - 1) / 2;
	const colWidth = (100 - spacers * spacerWidth) / cols;
	const itemSpan = widgetAttrs?.itemSpan;
	const rows = React.useMemo(() => {
		let cellIndice = 0;
		const childs = items.map((x) =>
			Object.assign({}, widgets[x] || {}, { id: x })
		);
		return getFullGrid(
			childs,
			gridSize,
			isStackLayout,
			itemSpan,
			isSpacer,
			draggedWidgetAttrsColSpan
		).map((columns, rowIndex) => {
			let span = 0;
			return columns.map((c) => {
				let columnIndex = span;
				span += c.separator ? 0 : Number(c.colSpan || 1);
				return { ...c, rowIndex, columnIndex, cellIndice: cellIndice++ };
			});
		});
	}, [
		items,
		widgets,
		gridSize,
		isStackLayout,
		itemSpan,
		isSpacer,
		draggedWidgetAttrsColSpan,
	]);

	const gridRows = rows.reduce((arr, row) => arr.concat(row), []);
	return (
		<GridContainer {...{ attrs, className }}>
			<table cellPadding="0" cellSpacing="0">
				{design && (
					<colgroup>
						{isStackLayout ? (
							<col width={"100%"} />
						) : (
							new Array(columns)
								.fill(0)
								.map((col, i) => (
									<col
										key={i}
										width={`${i % 2 === 1 ? colWidth : spacerWidth}%`}
									/>
								))
						)}
					</colgroup>
				)}
				<tbody>
					{rows.map((cols, rowIndex) => (
						<tr
							className={rowIndex % 2 === 0 ? "empty-row" : "fill-row"}
							key={rowIndex}
						>
							{cols.map(
								(
									{
										id,
										colSpan,
										cellIndice: indice,
										columnIndex,
										separator,
										relatedColSpan,
									},
									i
								) => {
									return (
										<td
											className={separator ? "separator" : ""}
											key={id ? id : "" + id + indice}
											colSpan={colSpan > 1 ? colSpan * 2 - 1 : colSpan}
										>
											{WidgetComponent && (
												<WidgetComponent
													design={design}
													meta={{
														isSeparator: Boolean(separator),
														colSpan,
														row: rowIndex,
														col: i,
														colIndex: columnIndex,
														grid: gridRows,
														total: columns,
														relatedColSpan: relatedColSpan,
													}}
													panelId={panelId}
													key={id ? id : "" + id + indice}
													id={id}
													index={indice}
													panelColumns={attrs.cols}
													_type={props._type}
													errorList={props.errorList}
													canRemove={props.canRemove}
												/>
											)}
										</td>
									);
								}
							)}
						</tr>
					))}
				</tbody>
			</table>
		</GridContainer>
	);
}

export default Grid;
